body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.container{
  width: 100%;
  padding-right: var(--bs-gutter-x, 0.75rem);
  padding-left: var(--bs-gutter-x, 0.75rem);
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 576px) {.container{max-width: 540px;} }
@media (min-width: 768px) {.container{max-width: 720px;} }
@media (min-width: 992px) {.container{max-width: 960px;} }
@media (min-width: 1200px) {.container{max-width: 1140px;} }
@media (min-width: 1400px) {.container{max-width: 1250px;} }

/* ---------------------------------------------------
  CSS START HERE
----------------------------------------------------*/

@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;500;600;700;800;900&family=Spline+Sans+Mono:wght@300;400;500;600;700&display=swap');

:root{
  --color-primary: #2168f7;
  --color-second: #47B5FF;
  --color-third: #DFF6FF;
  --color-dark: #0A1931;
  --color-gray: rgb(69, 69, 69);
  --color-white: rgb(255, 255, 255);
  --color-light: #929292;
  --color-shadow: rgb(33, 104, 247,.2);


  /* --color-dark: #eeeeee;
  --color-light: #606060; */
}

*{
  padding: 0;
  margin: 0;
  font-family: raleway;
}
html{
  background: var(--color-dark);
  overflow-x: hidden;
}
a{
  text-decoration: none;
  cursor: pointer;
}
button{
  cursor: pointer;
}
::-webkit-scrollbar{
  width: 2px;
}

/* ----------------------------------------------------
  Header Section
-----------------------------------------------------*/
.header .container{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .logo{
  color: var(--color-primary);
  font-size: 26px;
  width: 56px;
  height: 56px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 800;
  background: url('./logo.svg') no-repeat;
  background-size: contain;
}
.header .logo span{
  color: rgb(0, 67, 160);
}
.header .nav{
  padding: 20px 0;
}
.header .nav .link{
  padding: 15px;
  color: var(--color-light);
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 2px;
  text-transform: uppercase;
  position: relative;
  transition: .3s ease-in-out;
  transition-delay: .1s;
}
.header .nav .link:hover{
  color: var(--color-primary);
}

/* Social Navigation */
.header .social-nav{
  position: fixed;
  bottom: 200px;
  right: 20px;
}
.header .social-nav .inner{
  display: flex;
  flex-direction: column;
  position: relative;
}
.header .social-nav .inner::before{
  content: "";
  width: 1px;
  height: 200px;
  background: var(--color-light);
  position: absolute;
  top: 100%;
  left: calc(50% - 1px);
}
.header .social-nav .iconify{
  color: var(--color-light);
  padding: 10px;
  position: relative;
  transition: .3s ease-in-out;
}
.header .social-nav .nav-link:hover > .iconify{
  color: var(--color-primary);
  top: -2px;
}

/* ----------------------------------------------------
  Hero Section
-----------------------------------------------------*/
.hero .container{
  height: calc(100vh - 58px);
  display: flex;
  flex-direction: column;
  justify-content: center;
background: url('./bg.svg') no-repeat;
}
.hero .small-text{
  color: var(--color-primary);
  font-weight: 400;
  font-size: 20px;
  margin: 10px 0px;
  width: 394px;
  position: relative;
  font-family: 'Fira Code';
}
.hero .small-text::before{
  display: none;
}
.hero .small-text::before{
  content: "";
  width: 200px;
  height: 1px ;
  background: var(--color-primary);
  position: absolute;
  left: 100%;
  top: calc(100% - 11px);
}
.hero .big-text{
  max-width: 55%;
  margin-top: 10px;
  color: var(--color-light);
  font-weight: 700;
  line-height: 60px;
  font-size: 50px;
  word-spacing: 4px;
}
.hero .big-text .sub{
  color: var(--color-primary);
}
.hero .para-text{
  color: var(--color-light);
  max-width: 40%;
  margin: 20px 0;
  line-height: 30px;
  font-size: 17px;
  word-spacing: 5px;
  font-weight: 500;
}
.hero .btn{
  width: 150px;
  height: 45px;
  margin-top: 10px;
  color: var(--color-primary);
  background: var(--color-dark);
  font-size: 18px;
  font-weight: 500;
  font-family: 'Fira Code';
  border-radius: 4px;
  border: 1px solid var(--color-primary);
  position: relative;
  transition: .4s ease-in-out;
  transition-delay: .1s;
}
.hero .btn::before{
  width: 146px;
  height: 42px;
  border: inherit;
  border-radius: inherit;
  transition: .2s ease-in-out;
  transition-delay: .1s;
  content: "";
  position: absolute;
  top: 10px;
  left: 10px;
  z-index: -1;
}
.hero .btn:hover{
  color: var(--color-primary);
  text-shadow: 0px 0px 10px var(--color-primary);
}
.hero .btn:hover::before{
  top: 0;
  left: 0;
  box-shadow: 0px 1px 10px var(--color-primary);
}

/* ----------------------------------------------------
  Headings
-----------------------------------------------------*/
.container{
  position: relative;
  height: auto;
}
.heading{
  color: var(--color-light);
  font-family: 'Courier New', Courier, monospace;
  font-weight: 700;
  font-size: 28px;
  width: 100%;
  display: flex;
  justify-content: flex-start;
}
.sub-heading{
  color: var(--color-gray);
  writing-mode: vertical-lr;
  position: absolute;
  left: -6.5%;
  transform: rotate(180deg);
  font-size: 80px;
  letter-spacing: 1px;
  opacity: .1;
  text-transform: uppercase;
}
.container .heading span{
  color: var(--color-primary);
  font-family: 'Courier New', Courier, monospace;
}

/* ----------------------------------------------------
  Skills Section
-----------------------------------------------------*/
.skills-list{
  max-width: 900px;
  padding: 70px 0;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.skills .skills-list .iconify{
  color: var(--color-light);
  padding: 20px;
  width: 70px;
  height: 70px;
}
.skills-list .skill{
  width: 100px;
  height: 100px;
  margin: 20px 30px;
  position: relative;
  border: 1px solid gray;
  border-radius: 15px;
}
.skills-list .skill:first-child{
  margin-left: 0;
}
.skills-list .skill:last-child{
  margin-right: 0;
}
.skills-list div{
  position: relative;
}
.skills-list .popovers{
  color: var(--color-white);
  position: absolute;
  bottom: 0;
  right: 0;
  background: var(--color-primary);
  padding: 0px 10px;
  border-radius: 5px;
  opacity: 0;
  font-weight: 600;
  font-size: 14px;
  transition: .5s ease;
  transform: scale(.5);
}
.skills-list div:hover > .skill{
  color: var(--color-primary);
  border-color: var(--color-primary);
}
.skills-list div:hover > .popovers{
  opacity: 1;
  transform: scale(1);
}

/* ----------------------------------------------------
  Project Section
-----------------------------------------------------*/
.project{
  display: flex;
  justify-content: center;
  margin: 50px 0px;
}
.project .left{
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  max-width: 400px;
  height: 225px;
}
.project .left .img{
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 10px;
  border: 10px solid var(--color-primarys);
  box-shadow: 0px 5px 10px var(--color-shadow);
}
.project .right{
  color: var(--color-light);
  margin: 0 5px;
  max-width: 500px;
  text-align: left;
}
.project .right .top{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.project .right .top .name{
  font-weight: 700;
  font-size: 28px;
  margin: 0 10px;
}
.project .small-text{
  font-size: 32px;
  font-weight: 700;
  padding: 10px;
  text-align: right;
}
.project .desc{
  font-size: 18px;
  max-width: 500px;
  padding: 10px;
  line-height: 30px;
  font-weight: 600;
  word-spacing: 4px;
}
.project h3{
  color: var(--color-light);
  margin: 0 10px;
}
.project ul{
  margin: 0 10px;
}
.project ul li{
  font-size: 15px;
  font-weight: 600;
  list-style-type: none;
  display: inline-block;
  margin:5px;
  padding: 2px 15px;
  border-radius: 5px;
  color: var(--color-primary);
  border: 1px solid var(--color-primary);
}
.btn-wrapper{
  display: flex;
  justify-content: flex-end;
  background: linear-gradient(90deg, rgba(0,0,0,0), inherit, inherit);
}
.btn-wrapper .btn{
  color: var(--color-light);
  padding: 10px;
}
.btn-wrapper .btn:hover{
  color: var(--color-primary);
}
.project:nth-child(even){
  flex-direction: row-reverse;
}
.project:nth-child(even) .top{
  flex-direction: row;
}
.project:nth-child(even) .right{
  text-align: right;
}
/* ----------------------------------------------------
  Contact Section
-----------------------------------------------------*/
.contact .inner{
  max-width: 900px;
  min-height: 400px;
  margin: auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.contact p{
  color: var(--color-light);
  font-size: 22px;
  font-weight: 600;
  line-height: 30px;
  word-spacing: 4px;
  letter-spacing: 1px;
}
.contact .inner div{
  display: flex;
  justify-content: center;
  align-items: center;
}
.contact .btn{
  width: 150px;
  height: 45px;
  margin-top: 30px;
  color: var(--color-primary);
  background: var(--color-dark);
  font-size: 18px;
  font-weight: 600;
  border-radius: 4px;
  border: 1px solid var(--color-primary);
  position: relative;
  transition: .5s ease-in-out;
  top: 5px;
}
.contact .btn:hover{
  color: var(--color-white);
  background: var(--color-primary);
  top: 0px;
  box-shadow: 0px 5px 15px var(--color-shadow);
}

.footer{
  padding: 10px;
  z-index: 2;
  position: relative;
}
.footer .container{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.footer p{
  color: var(--color-white);
  font-family: 'raleway';
  word-spacing: 2px;
}



@media (max-width: 762px) {
  .hero .small-text{
    width: 100%;
  }
  .hero .big-text{
    max-width: 100%;
  }
  .hero .para-text{
    max-width: 100%;
  }
  .hero .btn{
    margin: 20px 0px;
  }
  .project .left{
    width: 40%;
    height: 100px;
  }
  .project .left .img{
    width: 100%;
    height: 100px;
  }
  .header .nav{
    display: none;
  }
}

@media (max-width: 576px) {
  @import url('https://fonts.googleapis.com/css2?family=Fira+Code:wght@300;400;500;600;700&display=swap');
  .header{
    padding: 10px 0;
  }

  .hero .small-text{
    font-size: 18px;
    text-align: left;
    font-family: 'Fira Code', monospace;
  }
  .hero .big-text{
    font-size: 36px;
    text-align: left;
    line-height: 47px;
  }
  .hero .para-text{
    text-align: left;
  }
  .hero .btn{
    margin: 30px 10px;
    width: 150px;
    height: 45px;
  }
  .hero .btn::before{
    width: 147px;
    height: 42px;
  }
  .skills .skill{
    margin: 10px 20px;
  }
  .skills .skills-list .iconify{
    width: 42px;
    height: 42px;
    padding: 15px;
  }
  .heading{
    font-size: 26px;
  }

  .projects{
    padding: 0;
    margin: 0 auto;
    max-width: calc(100% - 20px);
    height: auto;
    overflow: hidden;
  }
  .project{
    display: flex;
    flex-direction: column;
    margin: 30px auto;
    padding: 5px;
    box-shadow: 0px 0px 3px var(--color-shadow);
    border-radius: 10px;
    overflow: hidden;
    max-width: 400px;
    transition: .4s ease-in-out;
    position: relative;
    top: 0;
  }
  .project:nth-child(even){
    display: flex;
    flex-direction: column;
    text-align: left;
  }
  .project .left{
    width: calc(100% - 10px);
    height: auto;
    margin: 5px;
    box-shadow: 0px 0px 0px ;
  }
  .project .left .img{
    width: 100%;
    height: fit-content;
    box-shadow: 0px 2px 10px var(--color-shadow);
  }
  .project:nth-child(even) .right{
    text-align: left;
  }
  .project .top{
    margin-top: 20px;
    margin-bottom: 10px;
  }
  .project .right .top .name{
    margin: 0px;
    font-size: 24px;
  }
  .project .desc{
    font-size: 15px;
    padding: 0;
  }
  .project ul{
    margin: 15px 0px;
  }
  .project ul li{
    font-size: 13px;
  }
  .project ul li:first-child{
    margin-left: 0px;
  }
  .contact .inner{
    width: calc(100% - 20px);
    max-width: 400px;
    min-height: 230px;
    margin: 50px auto;
    display: grid;
    grid-template-columns: 1fr;
  }
  .contact p{
    font-weight: 500;
    letter-spacing: .5px;
    font-size: 16px;
  }
  .container{
    width: calc(100% - 25px);
  }
  .social-nav{
    display: none;
  }
  .project:hover{
    box-shadow: 0px 3px 10px var(--color-shadow);
    position: relative;
    top: -10px;
  }
  .footer{
    padding-top: 100px;
  }
  .footer p{
    font-size: 14px;
    padding: 20px 0;
    color: var(--color-light);
  }
}